import { Inject, Injectable, InjectionToken, Optional } from '@angular/core';
import {
  HttpEvent,
  HttpInterceptor,
  HttpHandler,
  HttpRequest,
  HttpClient,
  HttpErrorResponse,
} from '@angular/common/http';

import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { AuthService } from './deprecated/auth.service';

import { createAutoRefresh } from './auto-refresh';
import { is401AndNotReauth } from './auth-utils';
import { DOCUMENT } from '@angular/common';

// invalid token interceptor configuration
export interface InvalidTokenConfig {
  // if true, responses with 401 status will be repeated after session refresh
  enableAutoRefresh?: boolean;
  // redirect
  redirectToLoginPage?: boolean;
}

export const INVALID_TOKEN_CONFIG = new InjectionToken<InvalidTokenConfig>('invalid-token-config');

@Injectable()
export class InvalidTokenInterceptor implements HttpInterceptor {
  private config: Required<InvalidTokenConfig> = {
    enableAutoRefresh: false,
    redirectToLoginPage: true,
  };
  private autoRefresh = createAutoRefresh(this.authService, this.httpClient);

  constructor(
    private authService: AuthService,
    private httpClient: HttpClient,
    @Optional() @Inject(INVALID_TOKEN_CONFIG) private injectedConfig: InvalidTokenConfig,
    @Inject(DOCUMENT) private document: any,
  ) {
    this.config = {
      ...this.config,
      ...(this.injectedConfig || {}),
    };
  }

  private checkRedirect() {
    if (this.config.redirectToLoginPage) {
      this.authService.logout({ redirectUri: this.document.location.href });
    }
  }

  private tryRefresh(req: HttpRequest<unknown>, err: HttpErrorResponse) {
    return this.autoRefresh.refreshAndRepeat(req).pipe(
      catchError(() => {
        this.checkRedirect();
        return throwError(err);
      }),
    );
  }

  intercept(req: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(req).pipe(
      catchError((err: HttpErrorResponse) => {
        if (is401AndNotReauth(err)) {
          if (this.config.enableAutoRefresh && !this.autoRefresh.failed) {
            return this.tryRefresh(req, err);
          }
          this.checkRedirect();
        }
        return throwError(err);
      }),
    );
  }
}
