import { Observable, from } from 'rxjs';
import { HttpClient, HttpEvent, HttpRequest } from '@angular/common/http';
import { AuthService } from './deprecated/auth.service';
import { tap, switchMap } from 'rxjs/operators';

export interface AutoRefreshService {
  failed: boolean;
  refreshAndRepeat: (req: HttpRequest<unknown>) => Observable<HttpEvent<any>>;
}

export function createAutoRefresh(authService: AuthService, httpClient: HttpClient) {
  let refresh$: Observable<void> | undefined;
  let refreshFailed = false;

  function refreshAndRepeat(req: HttpRequest<unknown>): Observable<HttpEvent<any>> {
    if (!refresh$) {
      refresh$ = from(authService.refresh()).pipe(
        tap(
          // after successful refresh remove reference as access token can expire again in the future
          () => (refresh$ = undefined),
          // flag refresh failure so we will not try auto refresh again
          () => (refreshFailed = true),
        ),
      );
    }
    return refresh$.pipe(
      switchMap(() => {
        // repeat request (presume that access token is updated after refresh)
        return httpClient.request(req.clone());
      }),
    );
  }

  return {
    failed: refreshFailed,
    refreshAndRepeat,
  };
}
